<template>
    <transition
        name="custom-classes-transition"
        enter-active-class="animated slideInUp"
        leave-active-class="animated slideOutDown"
    >
        <div id="comments-content">
            <div class="hot-comment">
                <header>
                    <span class="hot-comments">{{
                        value == "hotComments" ? "热门评论" : "评论"
                    }}</span>
                    <select name="comment" v-model="value">
                        <option value="hotComments">热门评论</option>
                        <option value="comments">评论</option>
                    </select>
                </header>
                <section v-show="comments != null">
                    <ul class="comments-main">
                        <li
                            class="comment"
                            v-for="(comment, index) in comments"
                            :key="index"
                        >
                            <div class="header">
                                <div class="thumb">
                                    <img :src="comment.user.avatarUrl" />
                                </div>
                                <div class="info">
                                    <h5 class="nick-name">
                                        {{ comment.user.nickname }}
                                    </h5>
                                    <span class="time">{{
                                        formatDate(comment.time)
                                    }}</span>
                                </div>
                                <div class="liked">
                                    <i
                                        v-if="comment.liked"
                                        class="fa fa-thumbs-up"
                                    ></i>
                                    <i v-else class="fa fa-thumbs-o-up"></i>
                                    <span>{{ comment.likedCount }}</span>
                                </div>
                            </div>
                            <div class="content">
                                {{ comment.content }}
                            </div>
                        </li>
                    </ul>
                </section>
                <div class="none-comment" v-show="comments.length == 0">
                    快抢沙发~~~~~~~~~~~
                </div>
            </div>
        </div>
    </transition>
</template>

<script>
export default {
    props: {
        currentSongId: Number,
        commentsStatus: Boolean,
    },
    data() {
        return {
            value: "hotComments",
            allComments: [],
        };
    },
    computed: {
        comments: function() {
            if (this.value == "hotComments") {
                return this.allComments.hotComments;
            } else {
                return this.allComments.comments;
            }
        },
    },
    methods: {
        formatDate: function(time) {
            var date = new Date(time);

            var year = date.getFullYear();
            var month = date.getMonth() + 1;
            var day = date.getDate();
            month = month > 9 ? month : "0" + day;
            day = day > 9 ? day : "0" + day;

            return year + "年 " + month + "月 " + day + "日";
        },
        getComments: function(id) {
            var comments = JSON.parse(
                localStorage.getItem("allComments" + id)
            );
            if (comments && comments.exipre > Date.now()) {
                this.allComments = comments.result;
            } else {
                this.axios
                    .get("/comment/music", {
                        params: {
                            id: id,
                        },
                    })
                    .then((response) => {
                        this.allComments = response.data;

                        localStorage.setItem(
                            "allComments" + this.currentSongId,
                            JSON.stringify({
                                exipre: Date.now() + 1 * 60 * 60 * 1000,
                                result: response.data,
                            })
                        );
                    });
            }
        },
    },
    watch: {
        commentsStatus: function(value) {
            if (!value) {
                this.value = "hotComments";
            }
        },
        currentSongId: function (value) {
            this.getComments(value);
        },
    },
    created() {
        this.getComments(this.currentSongId);
    },
};
</script>

<style lang="less" scoped>
#comments-content {
    position: absolute;
    left: -92%;
    bottom: 60%;
    width: 95vw;
    background: white;
    z-index: 9999;
    color: #e4393c;
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.3);
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    .hot-comment {
        padding: 10px 15px;
        width: 100%;
        max-height: 300px;
        overflow-y: scroll;
        header {
            display: flex;
            justify-content: space-between;
            span {
                font-size: 16px;
                line-height: 30px;
            }
            select {
                width: 90px;
                height: 30px;
            }
        }
        section {
            margin: 10px 0;
            max-height: 200px;
            overflow-y: scroll;
            .comment {
                padding: 10px 0 15px;
                border-bottom: 1px solid #eee;
                display: flex;
                flex-direction: column;
                .header {
                    display: flex;
                    .thumb {
                        flex: 2;
                        display: flex;
                        max-width: 48px;
                        justify-content: center;
                        align-items: center;
                        margin: 5px 10px 5px 0;
                        img {
                            max-width: 48px;
                            display: block;
                            border-radius: 50%;
                        }
                    }
                    .info {
                        flex: 8;
                        padding-top: 15px;
                        h5 {
                            font-size: 18px;
                            color: #666;
                        }
                        span {
                            vertical-align: top;
                            font-size: 11px;
                            color: #999;
                        }
                    }
                    .liked {
                        flex: 2;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        color: #848485;
                        font-size: 22px;
                        span {
                            font-size: 16px;
                            vertical-align: middle;
                            margin-left: 5px;
                            color: #2c3e50;
                        }
                    }
                }
                .content {
                    padding-left: 50px;
                    font-size: 15px;
                    letter-spacing: 1px;
                    color: #2c3e50;
                }
            }
        }
    }
    .none-comment{
        font-size: 16px;
        text-align: center;
    }
}
</style>
