<template>
    <li class="songitem" @click="$emit('play-song', songItem)">
        <span class="order" v-if="order !== undefined">{{ order + 1 }}</span>
        <div
            class="main"
            :style="{ marginLeft: order !== undefined ? '0px' : '10px' }"
        >
            <h3>
                {{ songItem.name }}
                <span class="alias">{{
                    isExists ? songItem.song.alias[0] : songItem.alia[0]
                }}</span>
            </h3>

            <div class="info">
                <i class="icon icon-quality"></i>
                <span class="artists">{{
                    isExists
                        ? joinArtists(songItem.song.artists)
                        : joinArtists(songItem.ar)
                }}</span>

                <span class="album"
                    >-{{
                        isExists ? songItem.song.album.name : songItem.al.name
                    }}</span
                >
            </div>
        </div>
        <img
            class="icon icon-play"
            style="background:none"
            v-if="currentSong && songItem.id === currentSong.id"
            src="../assets/playing.svg"
            alt
        />
        <i v-else class="icon icon-play"></i>
    </li>
</template>

<script>
export default {
    props: {
        order: Number,
        songItem: Object,
        currentSong: Object,
    },
    computed: {
        isExists: function() {
            if (this.songItem.song) {
                return true;
            } else {
                return false;
            }
        },
    },
    methods: {
        joinArtists: function(artists) {
            return artists.map((artist) => artist.name).join(" / ");
        },
    },
};
</script>

<style lang="less" scoped>
.icon {
    background-image: url("../assets/index_icon_2x.png");
    display: inline-block;
    background-size: 166px auto;

    &.icon-quality {
        width: 12px;
        height: 8px;
        margin: 0 5px 0 2px;
    }
    &.icon-play {
        width: 25px;
        height: 25px;
        background-position-x: -23px;
        margin: 0 10px;
    }
}

.songitem {
    display: flex;
    margin: 10px 0;
    align-items: center;

    .order {
        font-size: 18px;
        width: 2em;
        text-align: center;
        color: rgb(189, 189, 189);
    }
    .main {
        flex: 1;
        width: 0;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        margin-left: 10px;
        h3 {
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
            .alias {
                color: rgb(189, 189, 189);
            }
        }
        .info {
            font-size: 12px;
            line-height: 2em;
            max-width: 260px;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
            color: rgb(189, 189, 189);
        }
    }
}
</style>
