<template>
    <div class="playbar">
        <!-- bottom -->
        <div class="playbarbottom" @click="showFullscreen = true">
            <img
                class="thumb"
                :class="{ active: playing }"
                :src="currentSongPicUrl"
                alt
            />
            <div class="info">
                <h5>{{ currentSongName }}</h5>
                <span>{{ currentSongArtists }}</span>
            </div>
            <div class="progress" @click.stop="playing = !playing">
                <canvas width="36" height="36" id="canvas"></canvas>
                <i v-if="playing" class="fa fa-pause"></i>
                <i v-else class="fa fa-play"></i>
            </div>
            <div class="playlist" @click.stop="showPlayList = !showPlayList">
                <i class="fa fa-bars" aria-hidden="true"></i>
            </div>
        </div>

        <!-- 播放列表 -->
        <transition
            name="custom-classes-transition"
            enter-active-class="animated slideInUp"
            leave-active-class="animated slideOutDown"
        >
            <ul v-if="showPlayList" class="defaultPlayList">
                <SongItem
                    v-for="(item, index) in defaultPlayList"
                    :key="index"
                    :songItem="item"
                    :options="{ order: index, info: false }"
                    @play-song="$emit('play-song', $event)"
                    :currentSong="currentSong"
                ></SongItem>
            </ul>
        </transition>

        <!-- fullscreen -->
        <transition
            name="custom-classes-transition"
            enter-active-class="animated fadeIn"
            leave-active-class="animated fadeOut"
        >
            <div v-show="showFullscreen" class="fullscreenplay">
                <div
                    class="mask"
                    :style="{
                        backgroundImage: `url(${currentSongPicUrl})`,
                        backgroundColor: 'black',
                    }"
                ></div>
                <header>
                    <i
                        class="fa fa-chevron-left"
                        @click="showFullscreen = false"
                    ></i>
                    <div class="info">
                        <h5>{{ currentSongName }}</h5>
                        <span>{{ currentSongArtists }}</span>
                    </div>
                    <i class="fa fa-share-alt"></i>
                </header>

                <div
                    v-show="showCircle"
                    class="circle"
                    :class="{ active: playing }"
                    @click="showCircle = false"
                >
                    <div class="needle"></div>
                    <div class="glue">
                        <img :src="currentSongPicUrl" alt />
                    </div>
                </div>
                <div
                    v-show="!showCircle"
                    class="lyric-box"
                    @click="showCircle = true"
                >
                    <div class="voice fa">
                        <span
                            v-if="volume > 0.5"
                            class="fa-volume-up volume"
                        ></span>
                        <span
                            v-else-if="volume < 0.5 && volume > 0"
                            class="fa-volume-down volume"
                        ></span>
                        <span v-else class="fa-volume-off volume"></span>
                        <div class="sound-progress">
                            <input
                                type="range"
                                v-model="volume"
                                value="0"
                                min="0"
                                step="0.01"
                                max="1"
                            />
                            <div
                                class="progress-active"
                                :style="{ width: volume * 100 + '%' }"
                            ></div>
                            <span :style="{ left: volume * 100 + '%' }"></span>
                        </div>
                    </div>
                    <ul class="scroll">
                        <li
                            v-for="(item, index) in parsedLyric"
                            :class="{
                                active: index === currentLyricIndex,
                                hidden: index + 4 < currentLyricIndex,
                            }"
                            :key="index"
                            :style="{
                                transform: `translateY(${-currentLyricIndex *
                                    40}px)`,
                            }"
                        >
                            {{ item.text }}
                        </li>
                    </ul>
                </div>

                <footer>
                    <div class="btns">
                        <div class="heart" @click="heart = !heart">
                            <i v-if="heart" class="fa fa-heart"></i>
                            <i v-else class="fa fa-heart-o"></i>
                        </div>
                        <div class="comment">
                            <Comments
                                :currentSongId="currentSong.id"
                                :commentsStatus="showComments"
                                v-show="showComments"
                            ></Comments>
                            <i
                                @click="showComments = !showComments"
                                class="fa fa-commenting"
                            ></i>
                        </div>
                        <div class="menu">
                            <i class="fa fa-ellipsis-v"></i>
                        </div>
                    </div>
                    <div class="duration">
                        <span class="start-time">{{
                            countTime(startTime)
                        }}</span>
                        <div class="progress">
                            <input
                                type="range"
                                value="0"
                                min="0"
                                step="0.01"
                                max="100"
                                v-model="progress"
                                @input="progressChange"
                            />
                            <div
                                class="progress-active"
                                :style="{ width: progress + '%' }"
                            ></div>
                            <span :style="{ left: progress + '%' }"></span>
                        </div>
                        <span class="end-time">{{ countTime(endTime) }}</span>
                    </div>
                    <div class="controls">
                        <div class="pattern">
                            <i
                                v-if="pattern"
                                class="fa fa-retweet"
                                @click="pattern = !pattern"
                            ></i>
                            <i
                                v-else
                                class="fa fa-random"
                                @click="pattern = !pattern"
                            ></i>
                        </div>
                        <div class="play-control">
                            <i
                                @click="prevSongIndex"
                                class="fa fa-step-backward"
                            ></i>
                            <i
                                v-if="!playing"
                                @click="playing = !playing"
                                class="fa fa-play"
                            ></i>
                            <i
                                v-else
                                @click="playing = !playing"
                                class="fa fa-pause"
                            ></i>
                            <i
                                @click="nextSongIndex"
                                class="fa fa-step-forward"
                            ></i>
                        </div>
                        <div class="list">
                            <i
                                @click.stop="showPlayList = !showPlayList"
                                class="fa fa-bars"
                                aria-hidden="true"
                            ></i>
                        </div>
                    </div>
                </footer>
            </div>
        </transition>

        <audio :src="currentSongUrl" autoplay></audio>
    </div>
</template>

<script>
import SongItem from "@/components/SongItem";
import Comments from "@/components/Comments";
export default {
    props: {
        currentSong: Object,
    },
    components: {
        SongItem,
        Comments,
    },
    data: function() {
        return {
            playing: false,
            showFullscreen: false,
            showCircle: false,
            currentLyric: null,
            currentLyricIndex: 0,
            showPlayList: false,
            defaultPlayList: [this.currentSong],
            volume: 0,
            heart: false,
            progress: 0,
            startTime: 0,
            endTime: 0,
            currentSongIndex: 0,
            pattern: true,
            showComments: false,
        };
    },
    methods: {
        joinArtists: function(artists) {
            return artists.map((artist) => artist.name).join(" / ");
        },
        drawProgress: function() {
            var canvas = this.$el.querySelector("#canvas");
            var audio = this.$el.querySelector("audio");
            var duration;
            if (this.currentSong.song) {
                duration = this.currentSong.song.duration;
            } else {
                duration = this.currentSong.dt;
            }

            audio.ontimeupdate = () => {
                /** @type {HTMLCanvasElement} */
                // 获取canvas元素
                var context = canvas.getContext("2d");
                context.clearRect(0, 0, 36, 36);

                context.beginPath();
                context.arc(18, 18, 17, 0, 2 * Math.PI);
                context.closePath();
                context.strokeStyle = "gray";
                context.lineWidth = "1";
                context.stroke();

                context.beginPath();
                context.arc(
                    18,
                    18,
                    17,
                    -0.5 * Math.PI,
                    -0.5 * Math.PI +
                        2 * Math.PI * ((audio.currentTime * 1000) / duration)
                );
                context.strokeStyle = "rgba(255,0,0,0.7)";
                context.lineWidth = "1";
                context.stroke();

                // 滚动歌词
                let index;

                if (this.parsedLyric != null) {
                    for (let i = 0; i < this.parsedLyric.length; i++) {
                        if (
                            audio.currentTime + 0.15 <
                            this.parsedLyric[i].time
                        ) {
                            index = i - 1;
                            break;
                        }
                    }
                    if (index === undefined) {
                        index = this.parsedLyric.length - 1;
                    }
                    this.currentLyricIndex = index;
                }

                // 更新进度条
                this.progress = (audio.currentTime / audio.duration) * 100;

                // 更新进度时间
                this.startTime = audio.currentTime;
            };
        },
        getLyric: function() {
            var cachedLyric = window.localStorage.getItem(
                "lyric" + this.currentSong.id
            );

            if (cachedLyric) {
                this.currentLyric = cachedLyric;
            } else {
                window.axios
                    .get("lyric", {
                        params: {
                            id: this.currentSong.id,
                        },
                    })
                    .then((response) => {
                        if (response.data.lrc) {
                            this.currentLyric = response.data.lrc.lyric;

                            window.localStorage.setItem(
                                "lyric" + this.currentSong.id,
                                response.data.lrc.lyric
                            );
                        } else {
                            this.currentLyric = null;
                        }
                    });
            }
        },
        countTime: function(value) {
            value = Math.floor(value);
            var m = Math.floor(value / 60);
            var s = value % 60;
            m = m > 9 ? m : "0" + m;
            s = s > 9 ? s : "0" + s;
            return m + ":" + s;
        },
        progressChange: function() {
            var audio = this.$el.querySelector("audio");
            audio.currentTime = (this.progress * audio.duration) / 100;
        },
        nextRandomSongIndex: function(index) {
            var nextRandom = Math.floor(
                Math.random() * this.defaultPlayList.length
            );
            if (index == this.defaultPlayList.length - 1) {
                return 0;
            } else if (
                nextRandom > index &&
                nextRandom <= this.defaultPlayList.length - 1
            ) {
                return nextRandom;
            } else if (nextRandom <= index) {
                return this.nextRandomSongIndex(index);
            }
        },
        prevRandomSongIndex: function(index) {
            var nextRandom = Math.floor(
                Math.random() * this.defaultPlayList.length
            );
            if (index == 0) {
                return this.defaultPlayList.length - 1;
            } else if (nextRandom < index && nextRandom >= 0) {
                return nextRandom;
            } else if (nextRandom >= index) {
                return this.prevRandomSongIndex(index);
            }
        },
        nextSongIndex: function() {
            if (this.defaultPlayList.length >= 1) {
                if (this.pattern) {
                    this.currentSongIndex++;
                    if (
                        this.currentSongIndex >
                        this.defaultPlayList.length - 1
                    ) {
                        // 重新从0播放
                        this.currentSongIndex = 0;
                    }
                } else {
                    this.currentSongIndex = this.nextRandomSongIndex(
                        this.currentSongIndex
                    );
                }
            }
        },
        prevSongIndex: function() {
            if (this.defaultPlayList.length >= 1) {
                if (this.pattern) {
                    this.currentSongIndex--;
                    if (this.currentSongIndex < 0) {
                        this.currentSongIndex = this.defaultPlayList.length - 1;
                    }
                } else {
                    this.currentSongIndex = this.prevRandomSongIndex(
                        this.currentSongIndex
                    );
                }
            }
        },
    },

    computed: {
        currentSongPicUrl: function() {
            if (this.currentSong.song) {
                return this.currentSong.picUrl;
            } else {
                return this.currentSong.al.picUrl;
            }
        },
        currentSongName: function() {
            return this.currentSong.name;
        },
        currentSongArtists: function() {
            if (this.currentSong.song) {
                return this.joinArtists(this.currentSong.song.artists);
            } else {
                return this.joinArtists(this.currentSong.ar);
            }
        },
        currentSongUrl: function() {
            if (this.defaultPlayList.length > 0)
                return `https://music.163.com/song/media/outer/url?id=${this.currentSong.id}.mp3`;
            else return "";
        },
        parsedLyric: function() {
            if (this.currentLyric != null) {
                return this.currentLyric.split("\n").map((item) => {
                    var regex = /\d{2}:\d{2}\.\d+/i;
                    if (item.search(regex) !== -1) {
                        var time = item.match(regex)[0];
                        var m = time.substr(0, 2);
                        var s = time.substr(3, 2);
                        var n = time.substr(5);
                        var text;
                        if (time.length == 9) {
                            text = item.substr(11);
                        } else if (time.length == 8) {
                            text = item.substr(10);
                        }
                        return {
                            time: Number(m) * 60 + Number(s) + Number(n),
                            text: text || "---",
                        };
                    } else {
                        return {};
                    }
                });
            } else {
                return [
                    {
                        time: 24 * 60 * 60 * 1000,
                        text: "暂无歌词",
                    },
                ];
            }
        },
    },
    created() {
        this.getLyric();
    },
    mounted() {
        var audio = this.$el.querySelector("audio");

        this.volume = audio.volume;

        this.drawProgress();

        audio.onerror = () => {
            alert("歌曲加载失败");
            if (this.currentSongIndex < this.defaultPlayList.length - 1) {
                this.currentSongIndex++;
            } else {
                this.currentSongIndex = 0;
            }
        }
        audio.onplay = () => {
            this.playing = true;
        };
        audio.onpause = () => {
            this.playing = false;
        };
        audio.ondurationchange = () => {
            this.endTime = audio.duration;
        };
        audio.onended = () => {
            if (this.defaultPlayList.length > 1) {
                if (this.pattern) {
                    this.currentSongIndex++;
                    if (
                        this.currentSongIndex >
                        this.defaultPlayList.length - 1
                    ) {
                        // 重新从0播放
                        this.currentSongIndex = 0;
                    }
                } else {
                    this.currentSongIndex = this.nextRandomSongIndex(
                        this.currentSongIndex
                    );
                }
            }
        };
    },

    watch: {
        playing: function(value) {
            var audio = this.$el.querySelector("audio");
            if (value) {
                audio.play();
            } else {
                audio.pause();
            }
        },
        currentSong: function(value) {
            this.getLyric();
            // 加入列表 需要过滤重复

            let isExist = this.defaultPlayList.some((item) => {
                return item.id == value.id;
            });

            if (!isExist) this.defaultPlayList.push(value);

            for (let i = 0; i < this.defaultPlayList.length; i++) {
                if (value == this.defaultPlayList[i]) {
                    this.currentSongIndex = i;
                }
            }
        },
        volume: function(value) {
            var audio = this.$el.querySelector("audio");
            audio.volume = value;
        },
        showFullscreen: function(value) {
            if (value) {
                this.showPlayList = false;
                this.showComments = false;
            } else {
                this.showComments = false;
            }
            this.$emit("translate-full-status", value);
        },
        currentSongIndex: function(value) {
            this.$emit("translate-song", this.defaultPlayList[value]);
        },
    },
};
</script>

<style lang="less" scoped>
@red: #d43c33;
@keyframes rotate {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}
.playbarbottom {
    padding: 5px 0;
    background: white;
    border-top: 1px solid rgba(128, 128, 128, 0.2);
    display: flex;
    align-items: center;
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    z-index: 10;
    .margin () {
        margin: 0 10px;
    }
    .thumb {
        .margin();
        width: 36px;
        height: 36px;
        border-radius: 50%;
        animation: rotate 6s linear infinite;
        animation-play-state: paused;
        &.active {
            animation-play-state: running;
        }
    }
    .info {
        flex: 1;
        font-size: 12px;
        line-height: 18px;
        h5 {
            max-width: 160px;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
        }
        span {
            display: inline-block;
            color: gray;
            max-width: 160px;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
        }
    }
    .progress {
        .margin();
        width: 30px;
        height: 30px;
        position: relative;
        #canvas {
            width: 100%;
        }
        i {
            position: absolute;
            top: 50%;
            left: 50%;
            color: #2c3e50;
            font-size: 13px;
            display: block;
            margin-top: -7px;
            margin-left: -4px;
            line-height: 1em;
            width: 12px;
            height: 12px;
            text-align: center;
            &.fa-pause {
                margin-top: -6px;
                margin-left: -6px;
            }
        }
    }
    .playlist {
        .margin();
        width: 30px;
        height: 30px;
        position: relative;
        border: 1px solid lightgray;
        border-radius: 50%;
        i {
            position: absolute;
            top: 50%;
            left: 50%;
            color: #2c3e50;
            font-size: 13px;
            display: block;
            margin-top: -6px;
            margin-left: -6px;
            line-height: 1em;
            width: 12px;
            height: 12px;
            text-align: center;
        }
    }
}

.defaultPlayList {
    position: fixed;
    bottom: 50px;
    left: 2.5%;
    width: 95%;
    max-height: 54%;
    overflow-y: scroll;
    background: white;
    border-radius: 10px;
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.3);
    z-index: 9999;
}

.fullscreenplay {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 999;
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    .mask {
        filter: blur(30px) brightness(0.5);
        transform: scale(2);
        width: 100%;
        height: 100%;
        background-size: cover;
        background-position: center;
        background-color: rgba(0, 0, 0, 0.3);
        position: absolute;
        z-index: -1;
    }
    input[type="range"] {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        opacity: 0;
        z-index: 9999;
        cursor: pointer;
    }
    header {
        display: flex;
        padding: 5px;
        i {
            flex: 1;
            font-size: 20px;
            color: rgba(255, 255, 255, 0.8);
            text-align: center;
            display: flex;
            align-items: center;
            &:hover {
                color: #fff;
            }
        }
        .info {
            flex: 10;
            font-size: 12px;
            letter-spacing: 1px;
            line-height: 18px;
            h5 {
                font-size: 14px;
                color: #fff;
                letter-spacing: 2px;
                max-width: 200px;
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
            }
            span {
                display: inline-block;
                color: gray;
                max-width: 200px;
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
            }
        }
    }
    .circle {
        flex: 1;
        text-align: center;
        position: relative;
        .glue {
            padding: 54px;
            background-image: url("../assets/disc_light-ip6.png"),
                url("../assets/disc-ip6.png");
            background-size: 100%;
            border-radius: 50%;
            display: inline-block;
            margin-top: 84px;
            animation: rotate 6s linear infinite;
            animation-play-state: paused;

            img {
                width: 180px;
                height: 180px;
                border-radius: 50%;
                vertical-align: middle;
                background-color: black;
            }
        }
        .needle {
            width: 96px;
            height: 137px;
            background-image: url("../assets/needle.png");
            background-size: 100%;
            position: absolute;
            top: 0;
            left: 50%;
            z-index: 1;
            margin-left: -16px;
            transform: rotate(-12deg);
            transform-origin: 16px 0;
            transition: all 0.3s;
        }
        &.active {
            .needle {
                transform: rotate(0deg);
            }
            .glue {
                animation-play-state: running;
            }
        }
    }
    .lyric-box {
        flex: 1;
        overflow: hidden;
        position: relative;
        .voice {
            padding: 5px;
            display: flex;
            justify-content: center;
            width: 80%;
            margin: 0 auto;
            .volume {
                display: inline-block;
                vertical-align: middle;
                width: 20px;
                height: 20px;
                color: #fff;
                margin-right: 5px;
            }
        }
        .sound-progress {
            width: 100%;
            height: 5px;
            margin-top: 6px;
            border-radius: 5px;
            background-color: rgba(255, 255, 255, 0.2);
            position: relative;
            .progress-active {
                position: absolute;
                left: 0;
                top: 0;
                width: 0;
                height: 100%;
                background-color: #0cb6d4;
                border-radius: 2px;
            }
            span {
                position: absolute;
                left: 0;
                top: 0;
                display: inline-block;
                vertical-align: middle;
                width: 8px;
                height: 100%;
                border-radius: 1px;
                background-color: #fff;
            }
        }

        .scroll {
            text-align: center;
            position: absolute;
            width: 100%;
            height: 100%;
            top: 50%;
            margin-top: 1.4em;
            li {
                max-width: 100%;
                word-break: break-all;
                line-height: 1.4em;
                font-size: 14px;
                height: 2.8em;
                font-weight: lighter;
                color: gray;
                transition: transform 0.3s;
                &.active {
                    color: white;
                }
                &.hidden {
                    opacity: 0;
                }
            }
        }
    }
    footer {
        height: 140px;
        .btns {
            display: flex;
            div {
                flex: 1;
                display: flex;
                justify-content: center;
                align-items: center;
                font-size: 28px;
                color: rgba(255, 255, 255, 0.8);
                margin: 15px 0;
                i.fa-heart {
                    color: @red;
                }
            }
            .comment {
                position: relative;
                left: 0;
                top: 0;
            }
        }
        .duration {
            display: flex;
            text-align: center;
            & > span {
                flex: 2;
                color: rgba(255, 255, 255, 0.6);
            }
            .progress {
                flex: 8;
                height: 8px;
                margin-top: 5px;
                background-color: rgba(255, 255, 255, 0.4);
                border-radius: 5px;
                position: relative;
                .progress-active {
                    position: absolute;
                    left: 0;
                    top: 0;
                    width: 0;
                    height: 100%;
                    background-color: #0cb6d4;
                    border-radius: 4px;
                }
                span {
                    position: absolute;
                    left: 0;
                    top: 0;
                    display: inline-block;
                    vertical-align: middle;
                    width: 8px;
                    height: 100%;
                    border-radius: 1px;
                    background-color: #fff;
                }
            }
        }
        .controls {
            display: flex;
            text-align: center;
            font-size: 28px;
            padding: 15px 0;
            color: rgba(255, 255, 255, 0.8);
            .pattern {
                flex: 2;
            }
            .play-control {
                flex: 8;
                i.fa-play {
                    margin: 0 40px;
                }
                i.fa-pause {
                    margin: 0 39px;
                }
            }
            .list {
                flex: 2;
            }
        }
    }
}
.fadeIn {
