<template>
    <div class="home">
        <div class="loading" v-if="showLoading">
            <img src="../assets/loading.svg" />
        </div>
        <NavLink></NavLink>
        <div class="songsheet">
            <CardTitle>推荐歌单</CardTitle>
            <ul class="songlist">
                <SongSheetCard
                    v-for="(item, index) in randomSongSheets()"
                    :key="index"
                    :item="item"
                    @play-song="$emit('play-song', $event)"
                    :currentSong="currentSong"
                ></SongSheetCard>
            </ul>
        </div>

        <div class="newsong">
            <CardTitle>最新音乐</CardTitle>
            <ul>
                <SongItem
                    v-for="(item, index) in newsongs"
                    :key="index"
                    :songItem="item"
                    @play-song="$emit('play-song', $event)"
                    :options="{ info: true }"
                    :currentSong="currentSong"
                ></SongItem>
            </ul>
        </div>
    </div>
</template>

<script>
// @ is an alias to /src
// import HelloWorld from "@/components/HelloWorld.vue";
import NavLink from "@/components/NavLink.vue";
import CardTitle from "@/components/CardTitle.vue";
import SongSheetCard from "@/components/SongSheetCard.vue";
import SongItem from "@/components/SongItem.vue";

export default {
    name: "Home",
    props:{
        currentSong: Object,
    },
    data: function() {
        return {
            songsheets: [],
            newsongs: [],
            showLoading: false,
            showControls: true,
        };
    },
    components: {
        // HelloWorld
        CardTitle,
        SongSheetCard,
        NavLink,
        SongItem,
    },
    methods: {
        // 获取推荐歌单
        getSongSheets: function() {
            this.axios
                .get("/personalized")
                .then((response) => {
                    // 第一次进入本组件时，先请求注入数据渲染页面，然后存入本地缓存中
                    this.songsheets = response.data.result;

                    // 把数据存进localStorage中,每次刷新页面就不用频繁地发请求去服务器拿数据
                    localStorage.setItem(
                        "songSList",
                        JSON.stringify({
                            // 过期时间 1小时
                            expire: Date.now() + 1 * 60 * 60 * 1000,
                            result: response.data.result,
                        })
                    );
                })
                .catch(function(error) {
                    console.log(error);
                });
        },
        // 获取最新歌曲
        getNewSong: function() {
            this.axios
                .get("/personalized/newsong")
                .then((response) => {
                    // 如果数据正确

                    this.newsongs = response.data.result;
                    // 获取到的数据放入本地存储
                    localStorage.setItem(
                        "newsongs",
                        // 过期时间1小时
                        JSON.stringify({
                            expire: Date.now() + 1 * 60 * 60 * 1000,
                            result: response.data.result,
                        })
                    );
                })
                .catch(function(error) {
                    console.log(error);
                });
        },

        randomSongSheets: function() {
            // 深拷贝数组
            var arr = [...this.songsheets];

            var newArr = [];
            for (let i = 0; i < 6; i++) {
                var r = Math.floor(Math.random() * arr.length);
                newArr.push(...arr.splice(r, 1));
            }
            return newArr;
        },
    },
    created() {
        // 先访问本地缓存中是否有数据
        var dataSongSheets = JSON.parse(localStorage.getItem("songSList"));

        if (dataSongSheets && dataSongSheets.expire > Date.now()) {
            // 存在且未过期
            this.songsheets = dataSongSheets.result;
        } else {
            // 已过期 或者 不存在
            this.getSongSheets();
        }

        var dataNewSongs = JSON.parse(localStorage.getItem("newsongs"));

        if (dataNewSongs && dataNewSongs.expire > Date.now()) {
            // 存在且未过期
            this.newsongs = dataNewSongs.result;
        } else {
            // 已过期 或者 不存在
            this.getNewSong();
        }
    },
    // 路由守卫
    beforeRouteEnter(to, from, next) {
        next((vm) => {
            vm.showLoading = false;
        });
    },

    beforeRouteLeave(to, from, next) {
        // 导航离开该组件的对应路由时调用
        // 可以访问组件实例 `this`
        this.showLoading = true;
        next();
    },
};
</script>

<style lang="less">
.songlist {
    display: flex;
    flex-wrap: wrap;
}
.home {
    position: relative;
}
.loading {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background: rgba(0, 0, 0, 0.4);
    display: flex;
    z-index: 999;
    justify-content: center;
    align-items: center;
    img {
        width: 36px;
    }
}
</style>
