<template>
    <div class="hot">
        <NavLink></NavLink>
        <div class="bg">
            <div class="hot-thumb">
                <div class="thumb"></div>
                <div class="time">更新时间:{{ time }}</div>
            </div>
        </div>
        <ul>
            <SongItem
                v-for="(item, index) in songList"
                :key="index"
                :songItem="item"
                @play-song="$emit('play-song', $event)"
                :order="index"
                :currentSong="currentSong"
            ></SongItem>
        </ul>
    </div>
</template>

<script>
import NavLink from "@/components/NavLink.vue";
import SongItem from "@/components/SongItem.vue";
export default {
    props: {
        currentSong: Object,
    },
    data: function() {
        return {
            showLoading: false,
            hotSong: null,
            songList: null,
        };
    },
    components: {
        NavLink,
        SongItem,
    },
    computed: {
        time: function() {
            if (this.hotSong != null) {
                return this.formatDate(this.hotSong.trackUpdateTime);
            } else {
                return "";
            }
        },
    },
    methods: {
        formatDate: function(time) {
            var date = new Date(time);

            var month = date.getMonth() + 1;
            var day = date.getDate();
            month = month > 9 ? month : "0" + day;
            day = day > 9 ? day : "0" + day;

            return month + "月 " + day + "日";
        },
    },
    watch: {
        hotSong: function(value) {
            let trackIds = [];
            for (let i = 0; i < value.trackIds.length; i++) {
                trackIds.push(value.trackIds[i].id);
            }

            window.axios
                .get("/song/detail", {
                    params: {
                        ids: trackIds.join(","),
                    },
                })
                .then((response) => {
                    // 如果数据正确

                    let songlist = response.data.songs;

                    this.songList = songlist;
                })
                .catch(function(error) {
                    console.log(error);
                });
        },
    },
    // 路由守卫
    beforeRouteEnter(to, from, next) {
        var hotSongData = JSON.parse(localStorage.getItem("hotSong"));
        if (hotSongData && hotSongData.exipre > Date.now()) {
            // 守卫回调
            next((vm) => {
                // 通过 `vm` 访问组件实例
                vm.hotSong = hotSongData.result;
            });
        } else {
            window.axios
                .get("/top/list", {
                    params: {
                        idx: 1,
                    },
                })
                .then((response) => {
                    // 如果数据正确
                    let playlist = response.data.playlist;

                    localStorage.setItem(
                        "hotSong",
                        JSON.stringify({
                            exipre: Date.now() + 1 * 60 * 60 * 1000,
                            result: playlist,
                        })
                    );
                    // 守卫回调
                    next((vm) => {
                        // 通过 `vm` 访问组件实例
                        vm.hotSong = playlist;
                    });
                })
                .catch(function(error) {
                    console.log(error);
                });
        }
    },
};
</script>

