.bg {
    width: 100vw;
    height: 142px;
    background-image: url("../assets/hot_music_bg_2x.jpg");
    background-size: 100%;
    background-repeat: no-repeat;
    position: relative;
    left: 0;
    top: 0;
    z-index: 0;

    &::after {
        content: "";
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
