    }
    .hot-thumb {
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        z-index: 2;
        display: flex;
        flex-direction: column;
        justify-content: center;
        margin-left: 20px;
        box-sizing: border-box;
        .thumb {
            width: 142px;
            height: 67px;
            background-image: url("../assets/index_icon_2x.png");
            background-position: -24px -30px;
            background-size: 166px 97px;
        }
        .time {
            margin-top: 10px;
            font-size: 12px;
            color: #fff;
        }
    }
}
</style>
