<template>
    <div class="palylist" v-if="songSheetDetail != null">
        <!-- palylist {{$route.query.id}} -->
        <div class="header">
            <div
                class="mask"
                :style="{
                    backgroundImage: `url(${songSheetDetail.coverImgUrl})`,
                }"
            ></div>
            <div class="fl">
                <img :src="songSheetDetail.coverImgUrl" alt />
                <span class="playcount">
                    <i class="fa fa-headphones"></i>
                    {{ songSheetPlayCount }}
                </span>
                <span class="icon">歌单</span>
            </div>
            <div class="fr">
                <h3>{{ songSheetDetail.name }}</h3>
                <img :src="songSheetDetail.creator.avatarUrl" alt />
                <span>{{ songSheetDetail.creator.nickname }}</span>
            </div>
        </div>

        <!--  -->
        <div class="info">
            <div class="tags">
                <span>标签：</span>
                <i v-for="(tag, index) in songSheetDetail.tags" :key="index">{{
                    tag
                }}</i>
            </div>
            <div class="desc" :class="{ show: showMoreDesc }">
                <span>简介：</span>
                <span>{{ songSheetDetail.description }}</span>
            </div>
            <div class="more" @click="showMoreDesc = !showMoreDesc">
                <i v-if="showMoreDesc" class="fa fa-angle-up"></i>
                <i v-else class="fa fa-angle-down"></i>
            </div>
        </div>
        <div class="list">
            <div class="title">歌曲列表</div>
        </div>
        <ul>
            <SongItem
                v-for="(item, index) in songDetail"
                :key="index"
                :songItem="item"
                @play-song="$emit('play-song', $event)"
                :order="index"
                :currentSong="currentSong"
            ></SongItem>
        </ul>
    </div>
</template>
<script>
import SongItem from "@/components/SongItem.vue";
export default {
    props: {
        currentSong: Object,
    },
    data: function() {
        return {
            songSheetId: this.$route.query.id,
            songSheetDetail: null,
            songDetail: null,
            showMoreDesc: false,
        };
    },
    components: {
        SongItem,
    },
    computed: {
        songSheetPlayCount: function() {
            return this.songSheetDetail.playCount > 100000000
                ? (this.songSheetDetail.playCount / 100000000).toFixed(1) + "亿"
                : this.songSheetDetail.playCount > 100000
                ? (this.songSheetDetail.playCount / 10000).toFixed(1) + "万"
                : this.songSheetDetail.playCount;
        },
    },
    beforeRouteEnter(to, from, next) {
        // 可以通过传一个回调给 next来访问组件实例。在导航被确认的时候执行回调，并且把组件实例作为回调方法的参数。
        // console.log(this)

        // 获取数据

        window.axios
            .get("/playlist/detail", {
                params: {
                    id: to.query.id,
                },
            })
            .then((response) => {
                // 如果数据正确

                let playlist = response.data.playlist;

                let trackIds = [];
                for (let i = 0; i < playlist.trackIds.length; i++) {
                    trackIds.push(playlist.trackIds[i].id);
                }

                window.axios
                    .get("/song/detail", {
                        params: {
                            ids: trackIds.join(","),
                        },
                    })
                    .then((response) => {
                        // 如果数据正确

                        let songlist = response.data.songs;

                        // 守卫回调
                        next((vm) => {
                            // 通过 `vm` 访问组件实例
                            vm.songSheetId = Number(to.query.id);
                            vm.songSheetDetail = playlist;
                            vm.songDetail = songlist;
                        });
                    })
                    .catch(function(error) {
                        console.log(error);
                    });
            })
            .catch(function(error) {
                console.log(error);
            });
    },
};
</script>

