<template>
    <li class="card" @click="routerToSongSheetPage(item.id)">
        <div class="thumb">
            <img :src="item.picUrl" />
            <span>
                <i class="fa fa-headphones"></i>
                {{ playCount }}
            </span>
        </div>
        <h5>{{ item.name }}</h5>
    </li>
</template>

<script>
export default {
    props: {
        item: {
            type: Object,
            default: () => {
                return {
                    id: 4969079749,
                    type: 0,
                    name: "欧美 | 欢快小调拯救你的不开心",
                    copywriter: "编辑推荐：音乐是治愈人心的良药",
                    picUrl:
                        "https://p2.music.126.net/nflx5ZxSSPivvGnyS5WRGw==/109951164906376339.jpg",
                    canDislike: false,
                    trackNumberUpdateTime: 1586934907095,
                    playCount: 1209355,
                    trackCount: 100,
                    highQuality: false,
                    alg: "featured",
                };
            },
        },
    },
    computed: {
        playCount: function() {
            return this.item.playCount > 100000000
                ? (this.item.playCount / 100000000).toFixed(1) + "亿"
                : this.item.playCount > 100000
                ? (this.item.playCount / 10000).toFixed(1) + "万"
                : this.item.playCount;
        },
    },
    methods: {
        routerToSongSheetPage: function(id) {
            this.$router.push({ path: "songsheet", query: { id: id } });
        },
    },
};
</script>

<style lang="less" scoped>
.card {
    flex-basis: 32%;
    flex-grow: 1;
    margin-bottom: 5px;
    &:nth-child(3n + 2) {
        margin: 0 1%;
    }
    .thumb {
        position: relative;
        img {
            vertical-align: middle;
        }
        span {
            position: absolute;
            top: 2px;
            right: 5px;
            color: white;
            opacity: 0.9;
            font-size: 13px;
        }
    }
    h5 {
        font-size: 14px;
        padding: 0 4px;
        margin: 3px 0 5px 0;
        line-height: 1.2em;
        height: 2.4em;
        overflow: hidden;
    }
}
</style>
