<template>
    <div
        id="app"
        :class="{ controlsFloat: showControls, overflow: fullScreenStatus }"
        @touchstart="touchstart"
        @touchend="touchend"
    >
        <!-- 一级路由出口 -->
        <keep-alive>
            <router-view
                @play-song="currentSong = $event"
                :currentSong="currentSong"
            ></router-view>
        </keep-alive>

        <!-- 播放器 控件 -->
        <PlayController
            @play-song="currentSong = $event"
            v-if="currentSong"
            :currentSong="currentSong"
            @translate-full-status="fullScreenStatus = $event"
            @translate-song="currentSong = $event"
        ></PlayController>
    </div>
</template>

<script>
import PlayController from "@/components/PlayController.vue";

export default {
    components: {
        PlayController,
    },
    data: function() {
        return {
            currentSong: null,
            touchstartX: 0,
            fullScreenStatus: false,
        };
    },
    computed: {
        showControls: function() {
            return this.currentSong == null ? false : true;
        },
    },
    methods: {
        touchstart: function(e) {
            if (this.fullScreenStatus) {
                return;
            }
            this.touchstartX = e.changedTouches[0].clientX;
            // this.touchstartY = event.changedTouches[0].clientY;
        },
        touchend: function(e) {
            if (this.fullScreenStatus) {
                return;
            }
            // 判断左滑动 返回上一页
            if (e.changedTouches[0].clientX - this.touchstartX > 50) {
                this.touchstartX = 0;
                // this.touchstartY = 0;
                this.routerBack();
            }

            // 判断右滑动 前进下一页
            if (e.changedTouches[0].clientX - this.touchstartX < -50) {
                this.touchstartX = 0;
                this.routerForward();
            }
        },
        routerBack: function() {
            this.$router.back();
        },
        routerForward: function() {
            this.$router.forward();
        },
    },
};
</script>

<style lang="less">
@red: #d43c33;
#app {
    font-family: Avenir, Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    // text-align: center;
    color: #2c3e50;
    &.controlsFloat {
        padding-bottom: 48px;
    }
    &.overflow {
        height: 100vh;
        overflow-y: hidden;
    }
}
</style>
