<template>
    <div class="search">
        <div
            class="loading"
            v-if="songList == null && text.length != 0 && suggest === null"
        >
            <img src="../assets/loading.svg" />
        </div>
        <NavLink></NavLink>
        <div class="sr">
            <i class="fa fa-search"></i>
            <i
                @click="text = ''"
                class="fa fa-times-circle"
                v-show="text.length != 0"
            ></i>
            <input
                type="text"
                placeholder="搜索歌曲、歌手、专辑"
                v-model="text"
            />
        </div>
        <div class="hot-keyword" v-show="text.length == 0">
            <div class="title">
                热门搜索
            </div>
            <ul class="hot-list">
                <li
                    @click="submitDefaultKeyWord(index)"
                    class="hot"
                    v-for="(hot, index) in hots"
                    :key="index"
                >
                    {{ hot.first }}
                </li>
            </ul>
        </div>
        <div
            class="suggest-keyword"
            v-show="text.length != 0 && songList == null"
        >
            <div @click="searchSong(text)" class="defaultSuggest">
                搜索"{{ text }}"
            </div>
            <ul class="suggest-list">
                <li
                    class="suggest"
                    v-for="(sug, index) in suggest"
                    :key="index"
                    @click="submitSuggestKeyWord(index)"
                >
                    <i class="fa fa-search"></i>
                    <span>{{ sug.keyword }}</span>
                </li>
            </ul>
        </div>
        <div class="song-list" v-show="songList != null && text.length != 0">
            <ul>
                <SongItem
                    v-for="(item, index) in songList"
                    :key="index"
                    :songItem="item"
                    @play-song="$emit('play-song', $event)"
                    :order="index"
                    :currentSong="currentSong"
                ></SongItem>
            </ul>
        </div>
    </div>
</template>

<script>
import NavLink from "@/components/NavLink.vue";
import SongItem from "@/components/SongItem.vue";
export default {
    props: {
        currentSong: Object,
    },
    data() {
        return {
            text: "",
            hots: null,
            suggest: null,
            songList: null,
        };
    },
    components: {
        NavLink,
        SongItem,
    },
    methods: {
        submitKeyWord: function(value) {
            window.axios
                .get("/search/suggest", {
                    params: {
                        keywords: value,
                        type: "mobile",
                    },
                })
                .then((response) => {
                    this.suggest = response.data.result.allMatch;
                })
                .catch(function(error) {
                    console.log(error);
                });
        },
        submitDefaultKeyWord: function(index) {
            this.text = this.hots[index].first;
            this.searchSong(this.hots[index].first);
        },
        submitSuggestKeyWord: function(index) {
            this.text = this.suggest[index].keyword;
            this.searchSong(this.suggest[index].keyword);
        },
        searchSong: function(value) {
            this.suggest = null;
            window.axios
                .get("/search", {
                    params: {
                        keywords: value,
                    },
                })
                .then((response) => {
                    let songs = response.data.result.songs;

                    let ids = [];
                    for (let i = 0; i < songs.length; i++) {
                        ids.push(songs[i].id);
                    }

                    window.axios
                        .get("/song/detail", {
                            params: {
                                ids: ids.join(","),
                            },
                        })
                        .then((response) => {
                            // 如果数据正确

                            this.songList = response.data.songs;
                        })
                        .catch(function(error) {
                            console.log(error);
                        });
                })
                .catch(function(error) {
                    console.log(error);
                });
        },
    },
    watch: {
        text: function(value) {
            if (value.length == 0) {
                this.songList = null;
            }

            let timer;
            timer = setTimeout(() => {
                clearTimeout(timer);
                this.submitKeyWord(value);
            }, 1000);
        },
    },
    // 路由守卫
    beforeRouteEnter(to, from, next) {
        window.axios
            .get("/search/hot")
            .then((response) => {
                // 如果数据正确
                let hots = response.data.result.hots;

                // 守卫回调
                next((vm) => {
                    // 通过 `vm` 访问组件实例
                    vm.hots = hots;
                });
            })
            .catch(function(error) {
                console.log(error);
            });
    },
    beforeRouteLeave(to, from, next) {
        // 导航离开该组件的对应路由时调用
        // 可以访问组件实例 `this`
        this.hots = null;
        this.suggest = null;
        this.songList = null;
        this.text = "";
        next();
    },
};
</script>

